.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 5em;
  padding-left: 1em;
  padding-right: 2.5em;
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity));
  --tw-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.homeButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

.homeButton>img {
  max-width: 45px;
  max-height: 45px;
}

.homeButton>span {
  font-family: 'Tangerine';
  font-size: 40px;
}

.icons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1.75rem;
}

.hubIcon {
  max-width: 53px;
  max-height: 53px;
  transition: transform .8s ease-in;
}

.hubIcon:hover {
  transform: rotateY(360deg)
}

.gitIcon {
  max-width: 40px;
  max-height: 40px;
  transition: transform .8s ease-in;
}

.gitIcon:hover {
  transform: rotateY(360deg)
}
.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  gap: 1rem;
}

.taunt {
  font-family: 'Tangerine';
  font-size: 50px;
  color: rgb(115, 115, 115);
}

.taunt>.apostrophe {
  font-size: 110px;
}
.playground {
  display: flex;
  flex-direction: column;
  margin-top: 2%;
  margin-left: 10%;
  margin-right: 10%;
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity));
  border-radius: 0.75rem;
  --tw-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.game {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2%;
}

.game>img {
  /*min 22/17*/
  min-width: 440px;
  min-height: 340px;
  width: 32%;
  height: 32%;
}

.inputZone {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.endText {
  margin-top: 1em;
  font-weight: bold;
  font-style: italic;
}

.endText>#mysteryWord {
  color: #009900;
}

.endText>#link {
  color: #a0e9a0;
  text-decoration: underline;
  cursor: pointer;
}

.userLetters {
  display: flex;
  justify-content: center;
  margin-top: 2em;
}

.userLetters>span {
  margin-left: 1em;
  margin-right: 1em;
}

.definition {
  margin-top: 2em;
}

.notClickedLetters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 2em;
  margin-left: 17%;
  margin-right: 17%;
}

.clickedLetters {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  margin-top: 2em;
  margin-bottom: 2em;
  margin-left: 17%;
  margin-right: 17%;
}

.rightLetters {
  display: flex;
  flex-wrap: wrap;
  color: #009900;
}

.wrongLetters {
  display: flex;
  flex-wrap: wrap;
  color: #990000;
}

.options {
  display: flex;
  margin-top: 3em;
  margin-bottom: 2em;
  margin-left: 5%;
  gap: 0.75rem;
}
